import React, { useContext, useState } from "react";
import "./Profile.css";
import { useNavigate } from "react-router-dom";
import KhelVideo from "../Video/KhelVideo";

import UserContext from "../../Context/UserContext";
const Profile = () => {
  const [isEditting, setIsEditting] = useState(false);
  const [isAdding, setIsAdding] = useState(false);
  const navigate = useNavigate();

  const playerData = [
    {
      name: "Surender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "Cricket",
    },
    {
      name: "Virender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "VolleyBall",
    },
    {
      name: "Mahender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "Cricket",
    },
    {
      name: "Gajender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "Cricket",
    },
    {
      name: "Dharamender Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTXh1J0Eqg9LnHiLd2x5DlLqQGkFLfqGgvYIQ&usqp=CAU",
      sport: "Cricket",
    },
    {
      name: "Harmender Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTGFAN7UKvo70IZwE_E99S4EiidVvU9BufSbQ&usqp=CAU",
      sport: "Hockey",
    },
    {
      name: "Nilender Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVNgQLU2XGU3A1gNd5IUFBDQD0-ZNi5rTFVg&usqp=CAU",
      sport: "Badminton",
    },
    {
      name: "Suresh  Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSJEYpUbdrjL33VF59VwvFI3Ys5im0PP1swww&usqp=CAU",
      sport: "Athletics",
    },
    {
      name: "Mahesh Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTPIaY5Vi88FDqXUC_-_XxqBjuwQdCa3SB8kQ&usqp=CAU",
      sport: "Cricket",
    },
  ];

  const videosData = [
    {
      name: "Surender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "Cricket",
      src: "https://www.youtube.com/embed/-YMo78jTbJw",
      title: "Surender playing Cricket",
    },
    {
      name: "Virender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "VolleyBall",
      src: "https://www.youtube.com/embed/dC7CD-Wrolg",
      title: "Virender playing VolleyBall",
    },
    {
      name: "Mahender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "Cricket",
      src: "https://www.youtube.com/embed/R27sZdC8gug",
      title: "Mahender playing Cricket",
    },
    {
      name: "Gajender Singh",
      image:
        "data:image/jpeg;base64,",
      sport: "Cricket",
      src: "https://www.youtube.com/embed/rW_fwcmyIfk",
      title: "Gajender playing Cricket",
    },
    {
      name: "Dharamender Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTXh1J0Eqg9LnHiLd2x5DlLqQGkFLfqGgvYIQ&usqp=CAU",
      sport: "Cricket",
      src: "https://www.youtube.com/embed/-YMo78jTbJw",
      title: "Dharamender playing Cricket",
    },
    {
      name: "Harmender Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTGFAN7UKvo70IZwE_E99S4EiidVvU9BufSbQ&usqp=CAU",
      sport: "Hockey",
      src: "https://www.youtube.com/embed/Zp_MHWTPC6I",
      title: "Harmender playing Hockey",
    },
    {
      name: "Nilender Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVNgQLU2XGU3A1gNd5IUFBDQD0-ZNi5rTFVg&usqp=CAU",
      sport: "Badminton",
      src: "https://www.youtube.com/embed/1JPlmLKsrB4",
      title: "Nilender playing Badminton",
    },
    {
      name: "Suresh  Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSJEYpUbdrjL33VF59VwvFI3Ys5im0PP1swww&usqp=CAU",
      sport: "Athletics",
      src: "https://www.youtube.com/embed/gkumUDj2MZg",
      title: "Suresh  playing Athletics",
    },
    {
      name: "Mahesh Singh",
      image:
        "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTPIaY5Vi88FDqXUC_-_XxqBjuwQdCa3SB8kQ&usqp=CAU",
      sport: "Cricket",
      src: "https://www.youtube.com/embed/OEkqzREtGKE",
      title: "Mahesh playing Cricket",
    },
  ];

  const profileUrl =
    "https://images.unsplash.com/photo-1494790108377-be9c29b29330?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MXx8dXNlciUyMHByb2ZpbGV8ZW58MHx8MHx8&w=1000&q=80";

  const EditProfile = () => {
    return (
      <div className="editprofile">
        <div className="editprofileSection">
          <button onClick={() => setIsEditting(false)}>
            <h2>X</h2>
          </button>
          <div className="editform">
            <label htmlFor="profilepic">Profilepic:</label>
            <input type="file" id="profilepic" />
          </div>
          <div className="editform">
            <label htmlFor="name">Name:</label>
            <input type="text" id="name" placeholder="Enter name" />
          </div>
          <div className="editform">
            <label htmlFor="description">Description:</label>
            <textarea
              name="description"
              id="description"
              cols="30"
              rows="10"
            ></textarea>
          </div>
          <button onClick={() => setIsEditting(false)}>Save</button>
        </div>
      </div>
    );
  };
  const AddVideo = () => {
    return (
      <div className="editprofile">
        <div className="editprofileSection">
          <button onClick={() => setIsAdding(false)}>
            <h2>X</h2>
          </button>
          <div className="editform">
            <label htmlFor="profilepic">Upload Video:</label>
            <input type="file" id="profilepic" />
          </div>
          <div className="editform">
            <label htmlFor="name">Title:</label>
            <input type="text" id="name" placeholder="Enter video title" />
          </div>
          <div className="editform">
            <label htmlFor="description">Sport:</label>
            <input type="text" id="name" placeholder="Enter sport" />
          </div>
          <div className="editform">
            Select player:
            <select name="" id="">
              {playerData.map((player) => {
                return (
                  <option value="" key={player.name + player.sport}>
                    {player.name}
                  </option>
                );
              })}
            </select>
          </div>
          <button onClick={() => setIsAdding(false)}>Upload</button>
        </div>
      </div>
    );
  };

  return (
    <>
      <div className="profilepage">
        <div className="profileSection">
          <div className="imageSection">
            <img src={profileUrl} alt="" />
          </div>
          <div className="dataSection">
            <h1>Coach Priti Kaur</h1>
            <h3>
              Priti is a highly experienced and successful senior leader having
              led and managed teams across the India. Priti has always had a
              passion for helping people to be the best they can and prides
              herself on changing the lives of many individuals. Priti has a
              natural professionalism and warmth that enables her to connect
              naturally with people. Her enthusiasm and positivity are
              infectious. Priti's approach to coaching is to authentically
              support each individual's growth through thought-provoking
              questions, great listening and empathy.
            </h3>
            <button onClick={() => setIsEditting(true)}>
              <h2>Edit Profile</h2>
            </button>
          </div>
        </div>
        {isEditting && <EditProfile />}
        {isAdding && <AddVideo />}
        <div className="profilefunctionality">
          <button
            onClick={() => {
              navigate("/createtalent");
            }}
          >
            <h1>Add Player</h1>
          </button>
          <button
            onClick={() => {
              setIsAdding(true);
            }}
          >
            <h1>Add Videos</h1>
          </button>
        </div>
        <div className="profilesectionheading">
          <h2>Players added by you</h2>
        </div>
        <div className="talentSection">
          {playerData.map((player) => {
            return (
              <div className="talent" key={player.name}>
                <div
                  className="talentimage"
                  onClick={() => {
                    navigate("/talent");
                  }}
                >
                  <img src={player.image} alt={player.name} />
                </div>
                <h3>{player.name}</h3>
                <h4>{player.sport}</h4>
              </div>
            );
          })}
        </div>
        <div className="profilesectionheading">
          <h2>Videos added by you</h2>
        </div>
        <div className="videoSection">
          {videosData.map((video) => {
            return (
              <div key={video.title}>
                <KhelVideo
                  src={video.src}
                  title={video.title}
                  player={video.name}
                  sport={video.sport}
                  rating="3.5"
                />
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default Profile;
